/**
 * zenSpa/scss/config.scss
 */
.welcome .welcome-bg {
  background: url(./static/img/home-bg.c5db186.jpg) center 0 no-repeat;
  background-size: cover;
  width: 100%;
  padding: 30px 0 18.54%; }
  .welcome .welcome-bg h3, .welcome .welcome-bg h2 {
    color: #fff; }
  .welcome .welcome-bg h2 {
    font-size: 56px; }

.welcome .welcome-container {
  max-width: 1024px;
  width: 95%;
  margin: 50px auto; }
  .welcome .welcome-container .welcome-detail-col {
    padding: 0 20px; }
    .welcome .welcome-container .welcome-detail-col .welcome-detail-col-title {
      font-size: 30px;
      text-align: center; }
    .welcome .welcome-container .welcome-detail-col .welcome-detail-col-text {
      font-size: 16px; }

@media only screen and (max-width: 575px) {
  .welcome .welcome-bg {
    background-position: center;
    padding: 30px 0 61.8%; }
  .welcome .welcome-detail-col {
    margin-bottom: 30px; }
    .welcome .welcome-detail-col:last-child {
      margin-bottom: 0; } }
